import { React, useState } from 'react';
import { Image, Button, Modal, Checkbox, Form, Input, Tabs } from 'antd';
import './DetailInfo.scss';
import { EyeInvisibleOutlined } from '@ant-design/icons';

const DetailInfo = ({ data }) => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const showModal = () => {
    setIsModalOpen(true);
  };
  const handleOk = () => {
    setIsModalOpen(false);
  };
  const handleCancel = () => {
    setIsModalOpen(false);
  };

  const onFinish = (values) => {
    console.log('Success:', values);
  };
  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };

  return (
    <div className='container'>
      <div className='img-container'>
        <Image
          width='100%'
          src={data.avatarUrl}
          fallback='data:image/png;base64,'
        />
      </div>
      <div className='info-container'>
        <div className='container_title'>
          <h1>thông tin cá nhân</h1>
          <div className='line'></div>
        </div>
        <div className='info-container_content'>
          <p className='info-title'>Họ và tên:</p>
          <p className='info-content'>{data.fullName}</p>
        </div>
        <div className='info-container_content'>
          <p className='info-title'>Mã số sinh viên:</p>
          <p className='info-content'>{data.studentId}</p>
        </div>
        <div className='info-container_content'>
          <p className='info-title'>Ngày sinh:</p>
          <p className='info-content'>
            {data.birthdate
              ? new Date(data.birthdate).toLocaleDateString('vi-VN')
              : 'Chưa cập nhật'}
          </p>
        </div>

        <div className='info-container_content'>
          <p className='info-title'>Lớp:</p>
          <p className='info-content'>
            {data.className ? data.className : 'Chưa cập nhật'}
          </p>
        </div>
        <div className='info-container_content'>
          <p className='info-title'>Email:</p>
          {data.email || data.personalEmail ? (
            <div className='email'>
              <a href={'mailto' + data.email} className='info-content'>
                {data.email}
              </a>
              <a href={'mailto' + data.personalEmail} className='info-content'>
                {data.personalEmail}
              </a>
            </div>
          ) : (
            <p className='info-content'>Chưa cập nhật</p>
          )}
        </div>
        <div className='info-container_content'>
          <p className='info-title'>Link CV:</p>
          {data.cvUrl ? (
            <a
              href={data.cvUrl}
              className='info-content'
              target='_blank'
              rel='noopener noreferrer'>
              Link CV
            </a>
          ) : (
            <p className='info-content'>Chưa cập nhật</p>
          )}
        </div>
        <div className='info-container_content'>
          <p className='info-title'>Link Facebook:</p>
          {data.profileUrl ? (
            <a
              href={data.profileUrl}
              className='info-content'
              target='_blank'
              rel='noopener noreferrer'>
              Link Facebook
            </a>
          ) : (
            <p className='info-content'>Chưa cập nhật</p>
          )}
        </div>
        <div className='info-container_content'>
          <p className='info-title'>Số điện thoại:</p>
          <p className='info-content'>
            {data.phoneNumber ? data.phoneNumber : 'Chưa cập nhật'}
          </p>
        </div>
        <div className='info-container_content'>
          <p className='info-title'>Địa chỉ:</p>
          <p className='info-content'>
            {/* {data.homeCommune && data.homeDistrict && data.homeProvince
              ? data.homeAddress
                ? data.homeAddress +
                  ', ' +
                  data.homeCommune +
                  ', ' +
                  data.homeDistrict +
                  ', ' +
                  data.homeProvince
                : data.homeCommune +
                  ', ' +
                  data.homeDistrict +
                  ', ' +
                  data.homeProvince
              : data.contactInfo
              ? data.contactInfo
              : 'Chưa cập nhật'} */}
            {data.contactInfo
              ? data.contactInfo
              : data.homeCommune && data.homeDistrict && data.homeProvince
              ? data.homeAddress
                ? data.homeAddress +
                  ', ' +
                  data.homeCommune +
                  ', ' +
                  data.homeDistrict +
                  ', ' +
                  data.homeProvince
                : data.homeCommune +
                  ', ' +
                  data.homeDistrict +
                  ', ' +
                  data.homeProvince
              : 'Chưa cập nhật'}
          </p>
        </div>
        <Button icon={<EyeInvisibleOutlined />} onClick={showModal}>
          Unhide
        </Button>
        <Modal
          title='Đăng nhập'
          open={isModalOpen}
          onOk={handleOk}
          onCancel={handleCancel}>
          <Form
            name='basic'
            labelCol={{
              span: 8,
            }}
            wrapperCol={{
              span: 16,
            }}
            initialValues={{
              remember: true,
            }}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
            autoComplete='off'>
            <Form.Item
              label='Username'
              name='username'
              rules={[
                {
                  required: true,
                  message: 'Please input your username!',
                },
              ]}>
              <Input />
            </Form.Item>

            <Form.Item
              label='Password'
              name='password'
              rules={[
                {
                  required: true,
                  message: 'Please input your password!',
                },
              ]}>
              <Input.Password />
            </Form.Item>

            <Form.Item
              name='remember'
              valuePropName='checked'
              wrapperCol={{
                offset: 8,
                span: 16,
              }}>
              <Checkbox>Remember me</Checkbox>
            </Form.Item>
          </Form>
        </Modal>
      </div>
      <div className='timetable-container'>
        <div className='container_title'>
          <h1>thời khoá biểu</h1>
          <div className='line'></div>
        </div>
        <div className='timetable-container_content'>
          <Tabs
            defaultActiveKey='1'
            items={new Array(7).fill(null).map((_, i) => {
              const id = String(i + 1);
              return {
                label: `${id === '1' ? 'Chủ nhật' : 'Thứ ' + id}`,
                key: id,
                children: (
                  <div className='class-item'>
                    <div className='class-time'>
                      <span>7:30</span>
                      <div className='v-line'></div>
                      <span>9:30</span>
                    </div>
                    <div className='class-info'>
                      <p className='class-name'>
                        123456 - Công nghệ phần mềm - IT1234
                      </p>
                      <span>Tuần học: 3-10,12-15,18-21</span>
                    </div>
                    <div className='class-room'>
                      <span>D8-103</span>
                    </div>
                  </div>
                ),
              };
            })}
          />
        </div>
      </div>
    </div>
  );
};

export default DetailInfo;
